body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(255,216,204);
  background: -moz-linear-gradient(202deg, rgba(255,216,204,1) 0%, rgba(255,219,155,1) 100%);
  background: -webkit-linear-gradient(202deg, rgba(255,216,204,1) 0%, rgba(255,219,155,1) 100%);
  background: linear-gradient(202deg, rgba(255,216,204,1) 0%, rgba(255,219,155,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffd8cc",endColorstr="#ffdb9b",GradientType=1);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
